.listBox {
  min-width: 400px;
  padding: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.text {
  font-size: 20px;
  margin-bottom: 16px;
}
.list {
  composes: listBox;
  list-style: none;
  gap: 6px;
}
.item {
  display: block;
  width: 100%;
  min-height: 26px;
  border-radius: 20px;
  background-color: #8080802e;
  text-align: center;
}
.link {
  color: #4a4a4a;
  font-size: 16px;
  text-decoration: none;
}
.item:hover,
.item:focus {
  background-color: #fff;
  box-shadow: 0 0 1px 2px #fff, 0 0 2px 4px #4a4a4a, 0 0 3px 6px #8080802e;
}
.movieBox {
  box-sizing: border-box;
  padding: 8px 14px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.img {
  width: 6%;
}
.title {
  font-family: 'Roboto';
  font-size: 20px;
}
